import Vue from 'vue'
import Router from 'vue-router'
import HcWrapper from '@/components/HcWrapper'

Vue.use( Router )

export default new Router( {
    routes: [
        {
            path: '/',
            name: 'HcWrapper',
            component: HcWrapper
        }
    ]
} )



